html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  min-height: 100svh;
  font-size: 1rem;
  font-family: Roboto, sans-serif;
  background: url("../img/type-writer.jpg") no-repeat center center/cover;
  color: var(--font-color);
  padding: 1.5rem;
}

h1,
h2,
h3 {
  font-weight: 400;
  line-height: 1.2;
  margin: 0;
}

h1 {
  font-size: clamp(1.375rem, 1rem + 2vw, 2.25rem);
}

label {
  font-size: clamp(1.25rem, 1rem + 2vw, 2rem);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --font-color: black;
  --background-color: white;
  --btn-hover-color: rgba(214, 205, 163, 0.5);
  --box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
}

.grid__new-task-entry #task-entry-form button[id=add-task], .grid__list-container .list-title button[id=clear-tasks] {
  min-width: 3rem;
  min-height: 3rem;
}

.btn, .grid__list-container .task .check-task, .grid__list-container .task .delete-task {
  background-color: var(--background-color);
  color: var(--font-color);
  border: 0.125rem solid black;
  border-radius: 0.5rem;
  outline: none;
  transition: all 0.5s ease-in-out;
}
.btn:hover, .grid__list-container .task .check-task:hover, .grid__list-container .task .delete-task:hover {
  cursor: pointer;
}
.btn:hover, .grid__list-container .task .check-task:hover, .grid__list-container .task .delete-task:hover, .btn:focus, .grid__list-container .task .check-task:focus, .grid__list-container .task .delete-task:focus {
  background-color: black;
  color: white;
  box-shadow: 0 0 0 0.25rem var(--btn-hover-color);
}

.grid {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: auto;
  gap: 1.5rem;
}
.grid__new-task-entry, .grid__list-container {
  background-color: var(--background-color);
  padding: clamp(1rem, 0.5rem + 2vw, 1.5rem);
  width: min(90% - 1rem, 50rem);
  margin-inline: auto;
  border-radius: 1rem;
  box-shadow: var(--box-shadow);
}
.grid__new-task-entry {
  position: sticky;
  top: 0;
}
.grid__new-task-entry #task-entry-form {
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
}
.grid__new-task-entry #task-entry-form label {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}
.grid__new-task-entry #task-entry-form input[id=new-task] {
  min-width: calc(80% - 1rem);
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  font-size: 1.25rem;
  border-width: 0.125rem;
  border-color: black;
  border-style: solid;
}
.grid__new-task-entry #task-entry-form input[id=new-task]:hover, .grid__new-task-entry #task-entry-form input[id=new-task]:focus {
  outline: 1px solid black;
  box-shadow: 0 0 0 0.25rem var(--btn-hover-color);
}
.grid__new-task-entry #task-entry-form button[id=add-task] {
  font-size: clamp(1.25rem, 0.75rem + 2vw, 1.5rem);
  padding: 0.5rem 0.75rem;
}
.grid__list-container {
  min-height: 60vh;
}
.grid__list-container .list-title {
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
  align-items: flex-end;
  margin-bottom: 0.5rem;
}
.grid__list-container .list-title button[id=clear-tasks] {
  font-size: clamp(1rem, 0.5rem + 2vw, 1.25rem);
  padding: 0.5rem 0.25rem;
}
.grid__list-container .list-title hr {
  color: black;
  width: 100%;
}
.grid__list-container .task {
  margin: 0.5rem 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 1rem;
}
.grid__list-container .task label {
  padding: 0.5rem;
  flex-basis: 75%;
}
.grid__list-container .task .checked {
  text-decoration: line-through;
}
.grid__list-container .task button {
  flex-shrink: 1;
  width: clamp(2rem, 1.5rem + 2vw, 3rem);
  height: clamp(1.5rem, 1rem + 2vw, 3rem);
  font-size: 1rem;
}
.grid__list-container .task .check-task {
  font-size: 1rem;
}
.grid__list-container .task .delete-task {
  font-size: 1rem;
}
.grid__list-container .confirmation {
  position: absolute;
  left: -100%;
}